import { Component, Input, OnInit, AfterViewInit, ViewEncapsulation } from '@angular/core';
import { environment } from '../../../../../environments/environment';
import {JSONBuilder} from '../../../+builder/services/JSONBuilder.service';
import {FeatureAuthService} from '../../../../shared/services/feature-access.service';
import {FeatureAccess} from "../../../../shared/interfaces/features.interface";
declare var jQuery: any;
@Component({
  selector: 'poweredby',
  template: `
		<div *ngIf="data.visible" >
      <div [ngClass]="{
                        'stickyfooter': true,
                        'lightSticky': !isBig() 
                      }" 
      [ngStyle]="{'background-color': isBig()?'#fff':'transparent'}"
      >
        <div class="col1" >
        
        </div>
        <div class="col">
          <div [ngClass]="{
                            'powered-by': true,
                            'lightPowered': !isBig() 
                          }">
            <span 
              *ngIf="windowWidth > 480">
                BUILT WITH
            </span>
            <a (click)="brandingClick($event)" href="javascript:void(0);" >
              <img alt="Powered By" src="assets/images/builder/og-logo.png" *ngIf="isBig()">
              <img alt="Powered By" src="assets/images/builder/og-logo-shadow.png" *ngIf="!isBig()">
              <span class="poweredby-text">Outgrow</span>
            </a>
          </div>
        </div>
      </div>
    </div>
	`,
  encapsulation: ViewEncapsulation.None
})

export class PoweredByComponent implements OnInit, AfterViewInit {
  @Input() data: any;
  @Input() devMode: any;
  url: string;
  windowWidth: any = jQuery(window).width();
  dataText: string;
  ngOnInit() {
    this.url = environment.PROTOCOL + environment.APP_EXTENSION;
    this.dataText = (this.windowWidth>480)?
      (this.jsonBuilderHelper.getJSONBuilt().templateType!='Recommendation'?'Create Your Own Calculator':'Create Your Own Quiz'):
      (this.jsonBuilderHelper.getJSONBuilt().templateType!='Recommendation'?'Create a Calculator':'Create a Quiz');
    if(!this.devMode) {
      this._featureAuthService.getPublicFeatureAccess().subscribe((result) => {
        this._featureAuthService.features = new FeatureAccess(result);
      });
    }
  }
  constructor(private jsonBuilderHelper:JSONBuilder,private _featureAuthService:FeatureAuthService){}

  brandingClick(event: any) {
    if (this.devMode)
      event.preventDefault();
    else
      window.open(this.url,'_blank');
  }

  isBig(){
    return <boolean>this._featureAuthService.features.custom_branding.logo_poweredby;
  }
  //
  // isSmall(){
  //   return <boolean>this._featureAuthService.features.custom_branding.cta_build_similar_calc;
  // }
  ngAfterViewInit() {
    let self = this;
    jQuery(window).on('resize', () => {
      self.windowWidth = jQuery(window).width();
      self.dataText = (self.windowWidth>480)?
        (self.jsonBuilderHelper.getJSONBuilt().templateType!='Recommendation'?'Create Your Own Calculator':'Create Your Own Quiz'):
        (self.jsonBuilderHelper.getJSONBuilt().templateType!='Recommendation'?'Create a Calculator':'Create a Quiz');
    });
  }
}



